import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule }   from '@angular/forms';
import { RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { InvoiceComponent } from './invoice/invoice.component';
import { OverlayPopupComponent } from './overlay-popup/overlay-popup.component';
import { RouteModule } from './route/route.module';
import {OverlayPanelModule} from 'primeng/primeng';
import {DialogModule} from 'primeng/primeng';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {CalendarModule} from 'primeng/primeng';
import { DateTimePickerModule} from 'ngx-datetime-picker';
import { Select2Module } from 'ng2-select2';
import { EventFoodComponentComponent } from './event-food-component/event-food-component.component';

import {HttpClientModule} from '@angular/common/http';
@NgModule({
  declarations: [
    AppComponent,
    InvoiceComponent,
    OverlayPopupComponent,
    EventFoodComponentComponent,

  ],
  imports: [
    FormsModule,
    BrowserModule,
    OverlayPanelModule,
    RouterModule,
    RouteModule,
    DialogModule,
    BrowserAnimationsModule,
    CalendarModule,
    DateTimePickerModule,
    Select2Module,
    HttpClientModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



